.main .react-datepicker__day--keyboard-selected {
  border-radius: 5px !important;
}

.react-datepicker__day--outside-month {
  color: #afafaf !important;
  pointer-events: none !important;
}

.react-datepicker-popper {
  /* inset: 18px auto auto 35px !important; */
  /* padding: 10px !important; */
}

.react-datepicker-popper[data-placement^='bottom'] .react-datepicker__triangle::before,
.react-datepicker-popper[data-placement^='bottom'] .react-datepicker__triangle::after {
  display: none !important;
}

.react-datepicker {
  background: #ffffff !important;
  border: 1px solid #e2e2ea !important;
  border-radius: 8px !important;
  /* margin-top: 30px !important; */
}

.react-datepicker__day-name,
.react-datepicker__day,
.react-datepicker__time-name {
  /* margin: 10px !important; */
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  color: #2d2d32;
}

.react-datepicker__day--selected {
  background-color: #57b894 !important;
  color: #ffffff;
}
.react-datepicker__day--in-selecting-range,
.react-datepicker__day--in-range,
.react-datepicker__month-text--selected,
.react-datepicker__month-text--in-selecting-range,
.react-datepicker__month-text--in-range,
.react-datepicker__quarter-text--selected,
.react-datepicker__quarter-text--in-selecting-range,
.react-datepicker__quarter-text--in-range,
.react-datepicker__year-text--selected,
.react-datepicker__year-text--in-selecting-range,
.react-datepicker__year-text--in-range {
  background-color: #57b993 !important;
  border-radius: 6px !important;
  font-size: 18px !important;
  color: white !important;
}
.react-datepicker__header {
  text-align: center;
  background-color: #ffffff !important;
  border-bottom: 0px solid #aeaeae !important;
  border-top-left-radius: 0.3rem;
  padding: 8px 0;
  position: relative;
}
